@import "../../../styles/varibles.scss";

.wrapper {
  width: 350px;
  height: 450;
  box-shadow: $boxShadow;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  overflow: hidden;
  background-color: $light;
}

.header {
  display: flex;
  height: 70px;
  padding: 20px;
  border-bottom: 2px solid $gray;
  background-color: $light;
  z-index: 1;
}

.dropdown {
  display: flex;
  flex-direction: column;
  padding: 10px;
  border-bottom: 1px solid $gray;
}

.dropdownHeader {
  display: flex;
  align-items: center;
  background-color: $light;
  z-index: 1;

  & > :last-child {
    transition: transform 0.3s;
    margin-left: 7px;
  }

  &[data-isDropdown="true"] {
    & > :last-child {
      transform: rotate(180deg);
    }
  }
}

.info {
  display: flex;
  padding: 10px;
}
