@import "../../../styles/varibles.scss";

.wrapper {
  height: 450px;
  width: 350px;
  background-color: $light;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: $boxShadow;
}

.header {
  background-color: $sheenGreen;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;

  span {
    font-size: 30px;
    font-weight: 300;
    color: $white;
  }
}

.dayNumber {
  font-size: 150px;
  color: $sheenGreen;
}
