@import "../../../styles/varibles.scss";

.day {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $light;
  cursor: pointer;
  transition: box-shadow 0.3s;

  &[data-is-today="true"] {
    border: 3px solid $sheenGreen;
  }

  &[data-is-clicked="true"] {
    background-color: $sheenGreen;
  }

  &:hover {
    // border: 1px solid $sheenGreen;
    box-shadow: 0px 0px 25px -5px $sheenGreen;
  }
}

.paddingDay {
  @extend .day;
  background-color: $lightGray;
  transition: background-color 0.3s;
  &:hover {
    // border: 1px solid $sheenGreen;
    background-color: $gray;
    box-shadow: none;
  }
}
