@import "../../../styles/varibles.scss";

.wrapper {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  width: 50%;
  box-shadow: $boxShadow;
  border-radius: 5px;
  overflow: hidden;
}

.calendarBody {
  padding: 20px;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 12px;
  background-color: $light;
  justify-items: center;
}
