@import "../../../styles/varibles.scss";

.navButton {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.addTask {
  height: 40px;
  border-radius: 4px;
  border: 2px solid $lightGray;
  transition: border 0.3s;

  span {
    margin-right: 10px;
  }

  &:hover {
    border: 3px solid $task;
  }
}

.addActivity {
  @extend .addTask;

  &:hover {
    border: 3px solid $activity;
  }
}

.addDeadline {
  @extend .addTask;

  &:hover {
    border: 3px solid $deadline;
  }
}

.addAnniversary {
  @extend .addTask;

  &:hover {
    border: 3px solid $anniversary;
  }
}
.addNote {
  @extend .addTask;

  &:hover {
    border: 3px solid $note;
  }
}

.addBirthday {
  @extend .addTask;

  &:hover {
    border: 3px solid $birthday;
  }
}
